* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
/* body{
    background-image: url(../asset/hero-back.webp);
      background-repeat: no-repeat;
    background-size: cover;
    opacity: 0.1;
} */
header{
    z-index: 1;
    position: fixed;
    background-color:whitesmoke;
}
#logo{
    height: 60px;
}
#logo-head{
    display: flex;
    justify-content: space-between;
    margin-left: 100px;
    margin-right: 100px;
    border-bottom: 1px solid #9725CC;
}
#nav-bar{
    margin-left: 100px;
    margin-right: 100px;
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
ul{
    margin-top: 30px;
    list-style-type: none;
    display: flex;
    gap: 20px;
}
button{
    border-radius: 10px;
    height: 35px;
    margin-top: -5px;

}
a{
    text-decoration: none;
    color: black;
}
a:hover{
    color: gray;
}
button{
    width: 130px   ;
}
#submit{
    background-color: #9725CC;
    color: whitesmoke;
}
#submit:hover {
    background-color: #7A1EAA;
}   
#register{
    border: 2px solid #9725CC;
    color: #9725CC;
}
#register:hover{
    background-color: #7A1EAA;
    color: whitesmoke;
}
main{
    height: 1200px;
    
    z-index: -1 ;
    /* opacity: 0.3; */
}
#hero-container{
    position: relative;
    z-index: -1;
    display: flex;
    top: 250px;
    justify-content: space-around;
}

#hero-img{
    height: 250px;
}
h1{
    font-size: 50px;
    margin-bottom: 20px;
}
span{
    color: #be4af3;
}
.btn{
    background-color: whitesmoke;
    color: #9725CC;
    border: 2px solid #9725CC;  
    border-radius: 12px;
    margin: 10px;
}
.btn:hover{
    background-color: #9725CC;
    color: whitesmoke;
}
#about{
    margin-top: 100px;
    text-align: center;
    margin-top: 330px;
}
h3{
    color: #9725CC;
    font-size: 20px;
    margin-bottom: 10px;
}
#txt-about{
    font-size: 40px;
    font-weight: bold;
    color: gray;
}
